<template>
<div class="main">
  {{msg}}
</div>
</template>

<script>
import NetValues from '../NetValues';
import io from 'socket.io-client';
export default {
  name: 'Main',
  data: () => {
    return {
      msg: '메인 페이지',
      show: true,
      form: {
        id: '',
        pw: '',
        now_time: 0
      }
    }
  },
  mounted() {
    let now = Date.now();
    let now2 = new Date('Sat Feb 23 2019 11:29:52 GMT+0900 (한국 표준시)');
    console.log('now:', now);
    console.log('now2:', now2);
    console.log(this.$moment().format("YYYY-MM-DD HH:mm:ss"));
    let u = this.$moment().valueOf();
    console.log('utc:', u.toString());
    console.log('1550986372599:', this.$moment(1550986372599).format("YYYY-MM-DD HH:mm:ss"));
    console.log(this.$moment());
    this.now_time = parseInt(this.$moment().valueOf());
    this.time_io = io('http://localhost:5000', {
      transports: ['websocket']
    });
    this.time_io.on('connect', () => {
      console.log('connection');
      this.time_io.emit('time_start', 'time_start');
      // you can also do this.messages.push(data)
    });
    this.time_io.on('msg', (data) => {
      data = parseInt(data);
      // this.time_io.disconnect();
      // you can also do this.messages.push(data)
    });
    this.time_io.on('time', (data) => {
      data = parseInt(data);
      console.log('data:', parseInt((data - this.now_time) / 1000));
      // this.time_io.disconnect();
      // you can also do this.messages.push(data)
    });
    this.time_io.on('disconnect', () => {
      console.log('disconnect');
      // you can also do this.messages.push(data)
    });
  },
  methods: {
    onSubmit: function() {
      console.log('onSubmit');
      let data = {
        id: this.form.id,
        pw: this.form.pw,
        time_io: null
      };
      this.$http.post('/api/login', data)
        .then((response) => {
          console.log('res:', response.data.res)
          if (response.data.res == NetValues.REQ_OK) {
            this.$router.push('/Campaign');
          } else {
            this.form.id = '';
            this.form.pw = '';
            alert('잘못된 정보입니다. 다시 입력해 주세요.');
            this.$refs.id.focus();
          }
        })
        .catch(function(error) {
          console.log('error');
        });
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}


.front_input {
  margin-top: 10px;
}
</style>



